

.homepage{
    height: 100vh;
    width: 100vw;
    /* background-color: rebeccapurple; */
    background-size: cover;
}

@media(min-width: 640px)
{
    .homepage .header{
        color: white;
        height: 100px;
        width: 100vw;
        display: flex;
        top: 0px;
        /* background-color: royalblue; */
        /* flex-direction: column;
        align-items: center; */
    }

}

@media(max-width: 640px)
{
    .homepage .header{
        color: white;
        padding-top: 20px;
        height: 100px;
        width: 100vw;
        display: flex;
        /* flex-direction: column;
        align-items: center; */
        /* background-color: royalblue; */
    }

}


.homepage .header .nav-link{
    width: 300px;
    /* background-color: gold; */
}

.homepage .header .title{
    height: 100%;
    padding: 20px;
    width: 65%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Bree Serif';
    font-size: 45px;
    /* background-color: blue; */
}

.homepage .header .dld-cont{
    height: 100px;
    position: relative;
    padding: 35px;
    /* background-color: gold; */
}


.homepage .download:hover{
    background-color: #c62a88;
    cursor: pointer;
}

.homepage .download{
    border-radius: 20px;
    background-color: transparent;
    color: white;
    outline: none;
    border: solid 1px white;
    padding: 10px;
    font-family: 'Bree Serif';
    font-size: 15px;
}

@media(max-width: 640px)
{
    .homepage .content{
        margin: 20px;
        height: 90vh;
        display: flex;
        flex-direction: row;
        align-items: center;
        color: white;
        font-family: 'Bree Serif';
    }

    .homepage .content .divider{
        height: 1px;
        width: 70%;
        margin: 20px 0px 20px;
        background-color: white;
    }

    .homepage .content .top{
        font-size: 25px;
        text-align: justify;
    }

    .homepage .content .bot{
        display: flex;
    }

    .homepage .content .inner{
        width: 200px;
    }
}

@media(min-width: 640px)
{
    .homepage .cont-content{
        width: 100vw;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .homepage .content{
        height: 90vh;
        display: flex;
        flex-direction: row;
        align-items: center;
        color: white;
        font-family: 'Bree Serif';
    }

    .homepage .content .divider{
        height: 1px;
        width: 50%;
        margin: 20px;
        background-color: white;
    }

    .homepage .content .top{
        font-size: 35px;
        text-align: center;
    }
    
    .homepage .cont-bot{
        width: 50vw;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .homepage .bot{
        display: flex;
    }

    .homepage .inner{
        font-size: 20px;
        width: 250px;
    }
}

.homepage .body .footer{
    background-color: #ffffff2f;
    display: flex;
    flex-direction: column;
    align-items: center;
    bottom: -10px;
    position: absolute;
    width: 100vw;
}
