.navbar-cont{
    height: 100px;
    width: 98vw;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    font-family: "Bree Serif";
    margin: 20px;
}

@media(max-width:640px){
    .navbar-cont{
        height: 200px;
        width: 98vw;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: fixed;
        font-family: "Bree Serif";
        margin: 20px;
    }
    
}

.nav-logo{
    height: 100px;
    width: 50%;
    display: flex;
    color: white;
}

.nav-logo .right, .left{
    height: 100px;
    width: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.right .title{
    font-size: 35px;
}

.left .btn-cont{
    display: inline;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 25px;
}

.left .btn-cont .download{
    font-family: "Bree Serif";
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 25px;
    outline: none;
    border: 1px black solid;
    padding: 2px 10px 2px;
    background-color: transparent;
    color: white;
    font-size: 17px;
}

.btn-cont:hover{
    background-color:  #c62a88;
    cursor: pointer;
}

.left .btn-cont .download .btn-items{
    margin: 5px;
}

.nav-menu{
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    color: white;
}

.nav-list{
    height: 100px;
    display: flex;
    flex-direction: row;
    list-style: none;
    align-items: center;
    color: white;
}

.nav-links{
    padding: 10px 20px 10px;
    margin: 10px;
    border-radius: 25px;
    font-size: 21px;
}

.nav-links:hover{
    background-color:  #c62a88;
    cursor: pointer;
}

a{
    text-decoration: none;
}

.android{
    color: white;
}